import React, { Component } from "react";
import { infoStations, detailStations } from "./planetData";

const StationContext = React.createContext();

class StationProvider extends Component {
  state = {
    stations: [],
    detailStations: detailStations,
    cart: [],
    modalOpen: false,
    modalStation: detailStations,
    cartSubTotal: 0,
    cartTax: 0,
    cartTotal: 0
  };

  componentDidMount() {
    this.setStations();
  }
  
  setStations = () => {
    let stations = [];
    infoStations.forEach(item => {
      const singleStation = { ...item};
      stations = [...stations, singleStation];
    });
    this.setState(() => {
      return { stations };
  
    }, 
    this.checkCartItem);
  };

  getItem = id => {
    const stations = this.state.stations.find(item => item.id === id);
    return stations;
  }

  handleDetail= id => {
    const stations = this.getItem(id);
    this.setState(() => {
      return {detailStations: stations};
    });
  };
  addToCart= id =>{
    let tempStations = [...this.state.stations];
    const index = tempStations.indexOf(this.getItem(id));
    const stations = tempStations[index];
    stations.inCart = true;
    stations.count = 1;
    const price = stations.price;
    stations.total = price;
    this.setState(() => {
      return {
        stations: [...tempStations],
        cart: [...this.state.cart, stations],
        detailStations: { ...stations }
      };
    }, this.addTotals);
  };



openModal = id => {
  const stations = this.getItem(id);
  this.setState(() => {
    return { modalStation: stations, modalOpen: true };
  });
};
closeModal = () => {
  this.setState(() => {
    return { modalOpen: false };
  });
};

increment = id => {
  let tempCart = [...this.state.cart];
  const selectedStations = tempCart.find(item => {
    return item.id === id;
  });
  const index = tempCart.indexOf(selectedStations);
  const stations = tempCart[index];
  stations.count += 1;
  stations.total += stations.price;
  this.setState(() => {
    return {
      cart: [...tempCart]
    };
  }, this.addTotals);
};
decrement = id => {
  let tempCart = [...this.state.cart];
  const selectedStations = tempCart.find(item => {
    return item.id === id;
  });
  const index = tempCart.indexOf(selectedStations);
  const stations = tempCart[index];
  stations.count -= 1;
  if (stations.count === 0) {
    this.removeItem(id);
  } else {
    stations.total = stations.count * stations.price;
    this.setState(() => {
      return { cart: [...tempCart] };
    }, this.addTotals);
  }
};

getTotals = () => {
  let subTotal = 0;
  this.state.cart.map(item => (subTotal += item.total));
  const tempTax = subTotal * 0.1;
  const tax = parseFloat(tempTax.toFixed(2));
  const total = subTotal + tax;
  return {
    subTotal,
    tax,
    total
  };
};
addTotals = () => {
  const totals = this.getTotals();
  this.setState(
    () => {
      return {
        cartSubTotal: totals.subTotal,
        cartTax: totals.tax,
        cartTotal: totals.total
      };
    },
  );
};

removeItem = id => {
  let tempStations = [...this.state.stations];
  let tempCart = [...this.state.cart];

  const index = tempStations.indexOf(this.getItem(id));
  let removedStations = tempStations[index];
  removedStations.inCart = false;
  removedStations.count = 0;
  removedStations.total = 0;

  tempCart = tempCart.filter(item => {
    return item.id !== id;
  });

  this.setState(() => {
    return {
      cart: [...tempCart],
      stations: [...tempStations]
    };
  }, this.addTotals);
};
clearCart = () => {
  this.setState(
    () => {
      return { cart: [] };
    },
    () => {
      this.setStations();
      this.addTotals();
    }
  );
};


  render() {
    return (
      <StationContext.Provider
        value={{
          ...this.state,
          handleDetail:this.handleDetail,
          addToCart: this.addToCart,
          openModal: this.openModal,
          closeModal: this.closeModal,
          increment: this.increment,
          decrement: this.decrement,
          removeItem: this.removeItem,
          clearCart: this.clearCart
        }}
      >
        {this.props.children}
      </StationContext.Provider>
    );
  }
}

const StationConsumer = StationContext.Consumer;

export { StationProvider, StationConsumer };



