import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import { BrowserRouter as Router } from "react-router-dom";
import "./index.css";
import {StationProvider} from './context'


ReactDOM.render(
  <StationProvider>
    <Router>
    <App />
  </Router>
  </StationProvider>,
  
  document.getElementById("root")
);



