import React, { Component } from "react";
import { StationConsumer } from "../context";
import { ButtonContainer } from "./Button";
import { Link } from "react-router-dom";
export default class Details extends Component {
  render() {
    return (
      <StationConsumer>
        {value => {
          const {id, company, img, info, price, title, inCart} = value.detailStations;

          return (
            <div className="container py-5">
              <div className="row">
                <div className="col-10 mx-auto text-center text-slanted text-blue my-5">
                  <h1>{title}</h1>
                </div>
              </div>
              <div className="row">
                <div className="col-10 mx-auto col-md-6 my-3">
                  <img src={img} className="img-fluid" alt="" />
                </div>
                <div className="col-10 mx-auto col-md-6 my-3 text-capitalize">
                  <h1>stations : {title}</h1>
                  <h4 className="text-title text-uppercase text-muted mt-3 mb-2">
                    société : <span className="text-uppercase">{company}</span>
                  </h4>
                  <h4 className="text-blue">
                    <strong>
                      prix (en Milliard de dollars) : <span>$</span>
                      {price}
                    </strong>
                  </h4>
                  <p className="text-capitalize font-weight-bold mt-3 mb-0">
                    informations :
                  </p>
                  <p className="text-muted lead">{info}</p>
                  <div>
                    <Link to="/shop">
                      <ButtonContainer>
                        retourner à la liste des stations
                      </ButtonContainer>
                    </Link>
                    <ButtonContainer
                      cart
                      disabled={inCart ? true : false}
                      onClick={() => {
                        value.addToCart(id);
                        value.openModal(id);
                      }}
                    >
                      {inCart ? "dans le panier" : "ajouter au panier"}
                    </ButtonContainer>
                  </div>
                </div>
              </div>
            </div>
          );
        }}
      </StationConsumer>
    );
  }
}



