import React, { Component } from 'react'
import Station from './Station'
import Title from './Title'
import {StationConsumer} from '../context'

export default class StationList extends Component {
    

    render() {
        
        return (
            <React.Fragment>
                <div className="py-5">
                    <div className="container">
                        <Title name="Nos" title="Stations" />
                        <div className="row">
                            <StationConsumer>
                                {value => {
                                   return value.stations.map(stations => {
                                       return <Station key={stations.id} stations={stations}
                                       />
                                   })
                                }}
                            </StationConsumer>
                        </div>
                    </div>
                </div>
            </React.Fragment>
               
            
        )
    }
}



